import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import validator from "validator";

import UIButton from "../../UI/UIButton/UIButton";

import { useAuth } from "../../../contexts/AuthContext";

import { Hidden, IconButton, TextField } from "@material-ui/core";

import { ArrowBackIos, FastRewind } from "@material-ui/icons";

import { useToastMessage } from "../../../contexts";

import { Signup } from "../../../api/user";

import { simpleEncrypt } from "../../../Shared/encryptResponse";

const UserInformation = (props) => {
  const { signInWithPassword } = useAuth();
  const history = useHistory();

  if (props) {
    console.log("pho", props.phone);
  }

  const toastMessage = useToastMessage();

  const [fields, setFields] = useState({
    mobile: props.phone,
    name: "",
    email: "",
    password: "",
    confirm_password: "",
  });
  const { name, email, password, confirm_password } = fields;

  let value;

  const HandleInputChange = (name) => (event) => {
    value = event.target.value;
    setFields({ ...fields, [name]: value });
  };

  const submit = async (e) => {
    e.preventDefault();
    toastMessage.setDisabled();

    if (!email) {
      return toastMessage.setErrorMessage("Email field required");
    }
    if (email) {
      if (!validator.isEmail(email)) {
        return toastMessage.setErrorMessage("Enter correct email address");
      }
    }
    if (!password) {
      return toastMessage.setErrorMessage("Enter Password");
    }

    if (password !== confirm_password) {
      return toastMessage.setErrorMessage(
        "Password not match with confirm password"
      );
    }

    const data = {
      mobile_number: `+${props.phone}`,
      name: fields.name,
      email: fields.email,
      password: fields.password,
      repeat_password: fields.confirm_password,
    };
    const res = await Signup(data);
    if (res.errors) {
      return toastMessage.setErrorMessage(
        res.messages ? res.messages.errors[0].message : res.message
      );
    }
    if (res.token) {
      localStorage.setItem("token", res.token);
      localStorage.setItem("userId", res.user.payload.id);

      window.location.href = "/business-setup";
      // return toastMessage.setErrorMessage(res.error);
    } else {
      return toastMessage.setErrorMessage(res.error);
    }
  };

  return (
    <>
      <Hidden mdUp implementation="js">
        <div className="form-header">
          <IconButton onClick={() => history.goBack()}>
            <ArrowBackIos />
          </IconButton>
          <div className="header-text">
            <h1>{"Sign up and unbox a pandora of marketing tools."}</h1>
          </div>
        </div>
      </Hidden>
      <form onSubmit={submit}>
        <div className="fields">
          <div className="col-10 col-sm-6 col-md-12">
            <TextField
              className="form-input mt-0"
              label="Mobile Number"
              variant="filled"
              fullWidth
              placeholder="Mobile Number"
              name="phone"
              value={props.phone}
            />
          </div>
          <div className="col-10 col-sm-6 col-md-12">
            <TextField
              className="form-input"
              label="Name"
              variant="filled"
              fullWidth
              placeholder="Name"
              name="name"
              value={name}
              onChange={HandleInputChange("name")}
            />
          </div>
          <div className="col-10 col-sm-6 col-md-12">
            <TextField
              className="form-input"
              label="Email"
              variant="filled"
              fullWidth
              placeholder="Email"
              name="email"
              value={email}
              onChange={HandleInputChange("email")}
            />
          </div>
          <div className="col-10 col-sm-6 col-md-12">
            <TextField
              className="form-input"
              label="Password"
              variant="filled"
              fullWidth
              placeholder="Set a new password"
              name="password"
              value={password}
              type="password"
              onChange={HandleInputChange("password")}
            />
          </div>
          <div className="col-10 col-sm-6 col-md-12">
            <TextField
              className="form-input"
              label="Confirm Password"
              variant="filled"
              fullWidth
              placeholder="Confirm password"
              name="confirm_password"
              value={confirm_password}
              type="password"
              onChange={HandleInputChange("confirm_password")}
            />
          </div>
          <div className="col-10 col-sm-6 col-md-12 Submit_Container">
            <UIButton style={{ marginLeft: 0 }} className="mt-4 ml-1 mb-5">
              Register
            </UIButton>
          </div>
        </div>
      </form>
    </>
  );
};

export default UserInformation;
