import { Card, TextField, Typography, withStyles } from "@material-ui/core";
import clsx from "clsx";
import { useHistory } from "react-router-dom";

import { Drawer, Hidden, IconButton } from "@material-ui/core";
import { ArrowBackIos } from "@material-ui/icons";

import React, { useEffect } from "react";
import { updateObject } from "../../Shared/Utility";
import UIButton from "../UI/UIButton/UIButton";
import firebase from "../../firebase";
import { useAuth } from "../../contexts/AuthContext";
import { useToastMessage } from "../../contexts";
import { useInterval } from "../../hooks/useInterval";
import { checkUserExistInDb } from "../../api/Auth";
import { simpleEncrypt } from "../../Shared/encryptResponse";

import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/material.css";

const NewUserSignup = (props) => {
  const {
    token,
    authSteps,
    setAuthStep,
    signInWithNumber,
    submitPhoneNumberAuthCode,
    checkUserAuthStatus,
    formData,
    setFormData,
    currentLoginState,
    resendOTP,
    setResendOTP,
  } = useAuth();

  const history = useHistory();

  const toastMessage = useToastMessage();

  const inputChangedHandler = (event, inputIdentifier) => {
    let updatedFormElement;

    updatedFormElement = updateObject(formData[inputIdentifier], {
      value:
        currentLoginState === "OTP"
          ? event.target.value.length < formData[inputIdentifier].maxLength
            ? event.target.value
            : event.target.value.slice(0, formData[inputIdentifier].maxLength)
          : event.target.value,
    });

    const updatedOrderForm = updateObject(formData, {
      [inputIdentifier]: updatedFormElement,
    });

    setFormData(updatedOrderForm);
  };

  const clearFieldValue = (fieldName) => {
    return setFormData((prevState) => ({
      ...prevState,
      [fieldName]: { ...prevState[fieldName], value: "" },
    }));
  };

  const onFormSubmit = async (e) => {
    e.preventDefault();

    toastMessage.setDisabled();

    // can't login with email using OTP
    if (!parseInt(formData.number.value) && currentLoginState === "OTP") {
      return toastMessage.setErrorMessage(
        "Cannot login with OTP using Email Address!"
      );
    }

    // these below code signs in with OTP
    if (authSteps === 0) {
      /* const res = await checkUserExistInDb({
        number: `+${formData.number.value}`,
      });
      if (res.error) {
        return toastMessage.setInfoMessage(res.error);
      } */
      return signInWithNumber(formData.number.value, toastMessage, false);
    }

    if (authSteps !== 0 && formData.otp.value.length > 5) {
      props.PhoneNumber(formData.number.value);
      const res = await submitPhoneNumberAuthCode(
        formData.otp.value,
        toastMessage,
        false
      );
      console.log("res gii", res);
    }
  };

  const authCaptchaVerifier = () => {
    window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(
      "captcha-container",
      {
        size: "invisible",
        callback: function (response) {
          signInWithNumber(formData.number.value, toastMessage);
        },
        "expired-callback": function () {
          // Response expired. Ask user to solve reCAPTCHA again.
          // ...
          alert("try again!");
        },
      }
    );
  };

  useEffect(() => {
    authSteps > 0 &&
      setFormData((prevState) => ({
        ...prevState,
        otp: { ...prevState.otp, disabled: false },
      }));
  }, [authSteps]);

  useEffect(() => {
    if (!token) {
      authCaptchaVerifier();
    }
    checkUserAuthStatus();
    setAuthStep(0);
  }, []);

  useInterval(
    () => {
      setResendOTP((prevState) => ({
        ...prevState,
        disabled: prevState.counter > 0 ? true : false,
        counter: prevState.counter - 1,
        startTimer: prevState.counter > 0 ? true : false,
      }));
    },
    authSteps === 1 &&
      currentLoginState === "OTP" &&
      resendOTP.counter >= 0 &&
      resendOTP.startTimer
      ? 1000
      : null
  );

  const inputChangedFlag = (event) => {
    let updatedFormElement;
    updatedFormElement = updateObject(formData["number"], {
      value: event,
    });
    const updatedOrderForm = updateObject(formData, {
      ["number"]: updatedFormElement,
    });
    setFormData(updatedOrderForm);
  };

  return (
    <>
      <Hidden mdUp implementation="js">
        <div className="form-header">
          <IconButton onClick={() => history.goBack()}>
            <ArrowBackIos />
          </IconButton>
          <div className="header-text">
            <h1>{"Sign up and unbox a pandora of marketing tools."}</h1>
          </div>
        </div>
      </Hidden>
      <form onSubmit={onFormSubmit}>
        <div className="fields">
          <div className={clsx("col-10 col-sm-6 col-md-12")}>
            <PhoneInput
              value={formData.number.value}
              country={"in"}
              containerClass="form-input mt-0"
              onChange={(phone) => inputChangedFlag(phone)}
            />
          </div>
          {authSteps === 1 && (
            <div className={clsx("col-10 col-sm-6 col-md-12")}>
              <TextField
                required
                className="form-input"
                label="OTP"
                type={formData.otp.type}
                onChange={(e) => inputChangedHandler(e, "otp")}
                value={formData.otp.value}
                name="otp"
                variant="filled"
                disabled={
                  currentLoginState === "OTP" ? formData.otp.disabled : false
                }
                pattern={currentLoginState === "OTP" && "[0-9]*"}
              />
            </div>
          )}
          {authSteps !== 0 && currentLoginState === "OTP" && (
            <div className="d-flex justify-content-end mt-1"></div>
          )}
          <div
            className={clsx("col-10 col-sm-6 col-md-12", "Submit_Container")}
          >
            <UIButton
              style={{ marginLeft: 0 }}
              disabled={
                toastMessage.isDisabled || formData.number.value.length < 10
              }
              className="mt-5 ml-1 mb-5"
            >
              {authSteps === 0 && currentLoginState === "OTP"
                ? "Get OTP"
                : "Verify"}
            </UIButton>
          </div>
        </div>
      </form>
      <div id="captcha-container"></div>
      <div className="already-signed-up">
        <h3 className="my-5">
          {"Already have an account? "}
          <a href="/login">{"Sign In"}</a>
        </h3>
      </div>
    </>
  );
};

export default NewUserSignup;
