import { useState } from "react";
import { Hidden } from "@material-ui/core";

import { useAuth } from "../../../contexts/AuthContext";

import NewUserSignup from "../../../Components/NewUserSignup";
import UserInformation from "../../../Components/NewUserSignup/UserInformation";

import image1 from "../assets/image-1.svg";

const SignUp = ({ className }) => {
  const [phone, setPhone] = useState(null),
    { authSteps } = useAuth();

  /*
  useEffect(() => {
    console.log('check', userDetail);
    if(userDetail.mobile && !userDetail.email){
      setPhone(userDetail.mobile);
      setAuthStep(2);
      }
  },[userDetail.mobile]) */

  return (
    <section className={className}>
      <div className="content">
        <Hidden smDown implementation="js">
          <div className="block-left">
            <div className="block-fixed">
              <h1>
                {"Sign up and unbox "}
                <br />
                {"a pandora of marketing tools."}
              </h1>
              <img src={image1} alt="image1" className="block-img" />
            </div>
          </div>
        </Hidden>
        <div className="block-right">
          {authSteps < 2 && (
            <NewUserSignup PhoneNumber={(number) => setPhone(number)} />
          )}
          {authSteps === 2 && <UserInformation phone={phone} />}
        </div>
      </div>
    </section>
  );
};

export default SignUp;
